import { Slide, Heading, Text, CodePane, Link } from 'spectacle';


function Less() {
  return (
    <Slide>
      <Heading fontSize="h1">Less</Heading>
      <Text color="secondary" ><strong>Show the contents of a file with pagination.</strong></Text>
      <Text>Requires a single argument, the path to the file</Text>
      <CodePane showLineNumbers={false} language='bash'>{`
      $ less sometext.txt

      # Use SPACE to page down
      # Use B to page up
      # Use UP and Down arrows to navigate line by line.
      # Use Q to quit
      # Use / to search the contents of the text
      `}</CodePane>
      <br />
      <Link textDecoration="none" textAlign="right" href="https://www.geeksforgeeks.org/less-command-linux/">[ Read More ]</Link>
    </Slide>
  );
}

export default Less;
