import { Slide, Heading, Text, CodePane, ListItem, UnorderedList, Link } from 'spectacle';


function Wildcard() {
  return (
    <Slide>
      <Heading fontSize="h1">Wildcard Matching</Heading>
      <Text color="secondary"><strong>Remove a file or files.</strong></Text>
      <UnorderedList>
        <ListItem>You can work on a set of files by using wildcard matching.</ListItem>
        <ListItem>the * (asterisk) represents anything.</ListItem>
        <ListItem>so, *.gif represents all files with a .gif extension.</ListItem>
        <ListItem>you can use wildcard matching with some of the commands that we’ve seen.</ListItem>
      </UnorderedList>
      <CodePane language='bash'>{`
      # copy all gifs into the archive folder
      $ cp *.gif archive

      # move all gifs into the archive folder
      $ mv *.gif archive
      `}</CodePane>
      <br />
      <Link textDecoration="none" textAlign="right" href="https://linuxhint.com/bash_wildcard_tutorial/">[ Read More ]</Link>
    </Slide>
  );
}

export default Wildcard;