import { Slide, Heading, Text, CodePane, Link, ListItem, UnorderedList } from 'spectacle';


function CP() {
  return (
    <Slide>
      <Heading fontSize="h1">Cp</Heading>
      <Text color="secondary" ><strong>Copy file to a path.</strong></Text>
      <UnorderedList>
        <ListItem>Think: <em>Copy</em></ListItem>
        <ListItem>Requires two arguments - pathnames for both source and destination.</ListItem>
        <ListItem>Can take a flag, -r, to copy recursively</ListItem>
      </UnorderedList>
      <CodePane showLineNumbers={false} language='bash'>{`
      $ cp filename.txt Desktop/

      $ cp filename.txt Desktop/filename2.txt
      
      $ cp -r dir1 dir2
      `}</CodePane>
      <br />
      <Link textDecoration="none" textAlign="right" href="https://www.geeksforgeeks.org/cp-command-linux/">[ Read More ]</Link>
    </Slide>
  );
}

export default CP;
