import { Slide, Heading, Text, CodePane, ListItem, UnorderedList, Link } from 'spectacle';


function RM() {
  return (
    <Slide>
      <Heading fontSize="h1">RM</Heading>
      <Text color="secondary"><strong>Remove a file or files.</strong></Text>
      <UnorderedList>
        <ListItem>Think - Remove</ListItem>
        <ListItem>Requires a single argument, the path to the file.</ListItem>
        <ListItem>Has an optional flag, -r, for removing recursively.</ListItem>
        <ListItem>Has an optional flag, -f, for skipping confirmation.</ListItem>
      </UnorderedList>
      <CodePane language='bash'>{`
      $ rm filename1.txt

      # also... to remove everything recursively (and ignore
      # confirmation)... use this (be careful with it!)

      $ rm -rf filename1.txt
      `}</CodePane>
      <br />
      <Link textDecoration="none" textAlign="right" href="https://www.geeksforgeeks.org/rm-command-linux-examples/">[ Read More ]</Link>
    </Slide>
  );
}

export default RM;