import { Slide, Heading, Text, CodePane, ListItem, UnorderedList, Link } from 'spectacle';


function Tail() {
  return (
    <Slide>
      <Heading fontSize="h1">Tail</Heading>
      <Text color="secondary"><strong>Show the contents of the end of a file.</strong></Text>
      <UnorderedList>
        <ListItem>Think - tail as the end of the file.</ListItem>
        <ListItem>Requires a single argument; the path to the file.</ListItem>
        <ListItem>Can be used with flags: -f (realtime) and -n (how many lines should be displayed?)</ListItem>
      </UnorderedList>
      <CodePane language='bash'>{`
      # show changes realtime!
      $ tail -f filename.txt
      
      # display last 5 lines
      $ tail -n5  filename.txt
      `}</CodePane>
      <br />
      <Link textDecoration="none" textAlign="right" href="https://www.geeksforgeeks.org/tail-command-linux-examples/">[ Read More ]</Link>
    </Slide>
  );
}

export default Tail;